import React, { Component } from 'react';
import Form from "react-jsonschema-form";
import json2md from 'json2md';
import MDIT from 'markdown-it';

// utils
function cleanArray(actual) {
  if (!actual) {
    return [];
  }
  let newArray = [];
  for (let i = 0; i < actual.length; i++) {
    if (actual[i]) {
      newArray.push(actual[i]);
    }
  }
  return newArray;
}
function isURL(str) {
  return /^(https?):\/\/((?:[a-z0-9.-]|%[0-9A-F]{2}){3,})(?::(\d+))?((?:\/(?:[a-z0-9-._~!$&'()*+,;=:@]|%[0-9A-F]{2})*)*)(?:\?((?:[a-z0-9-._~!$&'()*+,;=:\/?@]|%[0-9A-F]{2})*))?(?:#((?:[a-z0-9-._~!$&'()*+,;=:\/?@]|%[0-9A-F]{2})*))?$/i.test(str);
}

function copyTextToClipboard(text) {
  let textArea = document.createElement("textarea");
  textArea.style.position = 'fixed';
  textArea.style.top = 0;
  textArea.style.left = 0;
  textArea.style.width = '2em';
  textArea.style.height = '2em';
  textArea.style.padding = 0;
  textArea.style.border = 'none';
  textArea.style.outline = 'none';
  textArea.style.boxShadow = 'none';
  textArea.style.background = 'transparent';
  textArea.value = text;
  document.body.appendChild(textArea);
  textArea.select();
  try {
    let successful = document.execCommand('copy');
    let msg = successful ? 'successful' : 'unsuccessful';
    console.log('Copying text command was ' + msg);
  } catch (err) {
    console.log('Oops, unable to copy');
  }
  document.body.removeChild(textArea);
}
// end utils

// consts
const md = new MDIT();

const schema = {
  "type": "object",
  "required": [
  ],
  "properties": {
    "summary": {
      "type": "array",
      "title": "",
      "items": {
        "type": "string",
      }
    },
    "thoughts": {
      "type": "array",
      "title": "",
      "items": {
        "type": "string",
      }
    },
    "tasks": {
      "type": "array",
      "title": "",
      "items": {
        "type": "object",
        "required": [
          "title"
        ],
        "properties": {
          "title": {
            "type": "string",
            "title": "",
          },
          "wiki": {
            "type": "string",
            "title": "",
            "format": "uri"
          },
          "prd": {
            "type": "string",
            "title": "",
            "format": "uri"
          },
          "releaseDate": {
            "type": "string",
            "title": "",
            "format": "date"
          },
          "status": {
            "title": "",
            "type": "string",
            "enum": [
              "",
              "",
              "",
              "",
              "",
              "",
            ],
            "default": "",
            "uniqueItems": true
          },
          "progress": {
            "title": "%",
            "type": "integer",
            "minimum": 0,
            "maximum": 100,
            "multipleOf": 10,
            "default": 0
          },
          "background": {
            "type": "array",
            "title": "",
            "items": {
              "type": "string",
            }
          },
          "target": {
            "type": "array",
            "title": "",
            "items": {
              "type": "string",
            }
          },
          "details": {
            "type": "array",
            "title": "",
            "items": {
              "type": "string",
            }
          },
          "problems": {
            "type": "array",
            "title": "",
            "items": {
              "type": "string",
            }
          }
        }
      }
    },
    "other": {
      "type": "array",
      "title": "",
      "items": {
        "type": "string",
      }
    },
    "plans": {
      "type": "array",
      "title": "",
      "items": {
        "type": "string",
      }
    },
  }
};

const uiSchema = {
  summary: {
    items: {
      "ui:placeholder": '',
      // "ui:widget": "textarea"
    }
  },
  thoughts: {
    items: {
      "ui:placeholder": ''
    }
  },
  plans: {
    items: {
      "ui:placeholder": ''
    }
  },
  other: {
    items: {
      "ui:placeholder": ''
    }
  },
  tasks: {
    items: {
      title: {
        'ui:placeholder': ''
      },
      background: {
        items: {
          'ui:placeholder': ''
        }
      },
      target: {
        items: {
          'ui:placeholder': ''
        }
      },
      details: {
        items: {
          'ui:placeholder': ''
        }
      },
      problems: {
        items: {
          'ui:placeholder': ''
        }
      },
      progress: {
        "ui:widget": "range",
        "ui:options": {
          inline: true
        }
      }
    }
  }
};

let codeMirror = null;

class App extends Component {
  constructor(props) {
    super(props);
    this.onUnload = this.onUnload.bind(this);
    this.state = {
      formData: {
        summary: [],
        thoughts: [],
        tasks: [],
        other: [],
        plans: []
      }
    }
  }
  render() {
    return (
      <div className="App col-md-12">
        <div className="col-md-5 form-section">
          <Form schema={schema}
            uiSchema={uiSchema}
            noValidate={true}
            formData={this.state.formData}
            onChange={({ formData }) => {
              this.setState({ formData });
              codeMirror.setValue(this.mdConvert(formData));
            }}
            onSubmit={(e) => false}>
            <button type="submit" style={{ display: 'none' }}></button>
          </Form>
        </div>
        <div className="col-md-7 fix-content">
          <div className="col-md-5 textarea-section">
            <legend>Markdown</legend>
            <textarea></textarea>
          </div>
          <div className="col-md-7 preview-section">
            {/* <button className="col-md-3 btn btn-info btn-sm" onClick={this.updatePreview}><span style={{ fontSize: 'normal' }}></span></button> */}
            <button className="col-md-3 btn btn-info btn-sm" onClick={this.copyMD}><span style={{ fontSize: 'normal' }}>md</span></button>
            <button id="cpBtn" className="col-md-3 btn btn-info btn-sm" onClick={this.copyContent}><span style={{ fontSize: 'normal' }}>html</span></button>
            <iframe id="preview" frameBorder="0" title=""></iframe>
          </div>
        </div>
      </div>
    );
  }
  mdConvert(data) {
    let dataArr = [];
    if (!data) {
      return '';
    }
    if (data.summary) {
      dataArr.push(
        { h3: "" },
        { ul: cleanArray(data.summary) },
      );
    }
    if (data.thoughts) {
      dataArr.push(
        {
          h3: ""
        }, {
          ul: cleanArray(data.thoughts)
        }
      );
    }
    let tasks = [];
    if (data.tasks) {
      data.tasks.map((item) => {
        tasks.push(
          {
            h4: `${(item.title || '')} \`${item.status}\``
          },
          {
            ul: cleanArray([
              `: <${isURL(item.wiki) ? item.wiki : ''}>`,
              `: <${isURL(item.prd) ? item.prd : ''}>`,
              `${item.releaseDate ? ': ' + item.releaseDate : ''}`,
              `${item.progress ? ': ' + item.progress + '%' : ''}`,
              `${item.background && item.background.length ? ':' : ''}`,
              {
                ul: cleanArray(item.background)
              },
              `${item.target && item.target.length ? ':' : ''}`,
              {
                ul: cleanArray(item.target)
              },
              `${item.details && item.details.length ? ':' : ''}`,
              {
                ol: cleanArray(item.details)
              },
              `${item.problems && item.problems.length ? ':' : ''}`,
              {
                ul: cleanArray(item.problems)
              },
            ])
          }
        );
        return false;
      });
      dataArr.push(
        { h3: "" }
      );
      dataArr.push(tasks);
    }

    if (data.other && data.other.length) {
      dataArr.push(
        { h3: "" },
        { ul: cleanArray(data.other) },
      );
    }
    if (data.plans && data.plans.length) {
      dataArr.push(
        { h3: "" },
        { ul: cleanArray(data.plans) },
      );
    }
    return json2md(dataArr);
  }
  updatePreview() {
    document.getElementById('preview').contentWindow.document.body.innerHTML = md.render(codeMirror.getValue());
  }
  copyMD() {
    copyTextToClipboard(codeMirror.getValue())
  }
  copyContent() {
    let iframeElement = document.getElementById('preview');
    let contentDoc = iframeElement.contentDocument;
    let range = contentDoc.createRange();
    range.selectNode(contentDoc.body);
    let selection = iframeElement.contentWindow.getSelection();
    selection.removeAllRanges();
    selection.addRange(range);
    contentDoc.execCommand('copy');
  }
  componentDidMount() {
    let _this = this;
    const lsJSON = JSON.parse(localStorage.getItem('WEEKLY'));
    this.setState({
      formData: lsJSON
    });

    codeMirror = window.CodeMirror.fromTextArea(document.querySelector('.textarea-section>textarea'), {
      lineNumbers: true,
      mode: {
        name: "markdown",
        highlightFormatting: true
      },
      lineWrapping: true,
      tabSize: 2,
      // theme: "default"
    });
    codeMirror.on('change', function(){
      _this.updatePreview();
    });
    codeMirror.setValue(this.mdConvert(lsJSON));

    //  
    let frame = document.getElementById('preview');
    let doc = frame.contentWindow.document;
    let link = doc.createElement("link");
    doc.title = 'HTML';
    link.href = 'https://dswwsd.github.io/markdown.css';
    link.type = "text/css";
    link.rel = "stylesheet";
    link.media = "screen,print";
    doc.getElementsByTagName("head")[0].appendChild(link);

    // iOS pagehide
    window.addEventListener("beforeunload", this.onUnload);

  }
  onUnload() {
    localStorage.setItem('WEEKLY', JSON.stringify(this.state.formData));
  }
}

export default App;



